.neighborhood-overview {
}
.neighborhood-overview .section {
    position: relative;
}
.neighborhood-overview .section .container {
    height: 100%;
}
/* Filters */
.neighborhood-overview .filters {
    position:fixed;
    z-index:1;
    width:100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    background-color: $blue_light;
    padding: 20px 0;
    height: 67px;
}
.neighborhood-overview .filters > .container {
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
                -ms-grid-row-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.neighborhood-overview .filters p {
    color: #fff;
    font-family: "Open sans";
    font-weight: 600;
    font-size: 18px;
    letter-spacing: -0.2px;
}
.neighborhood-overview .filters p, .neighborhood-overview .filters select, .neighborhood-overview .filters .button {
    margin-left: 10px;
    margin-right: 10px;
}
/* Map & Info window */
.neighborhood-overview .map {
    background-color: $cream;
    height:100vh;
    position:fixed;
    overflow:hidden;
    width: 100%;
    margin-top:67px;
}
.neighborhood-overview .overview-map {
    height: 100%;
}
/* Hide close button automatically inserted */
.neighborhood-overview .infoBox > img {
    display: none;
}
.neighborhood-overview .info-window {
    background-color: #fff;
    border: 3px solid $purple;
    margin: 0 auto;
    padding: 30px;
    position: absolute;
    left: -50%;
    top: 15px;
    text-align: center;
    width: 320px;
    z-index: 2;
}
/* Listings */
.neighborhood-overview .listings-wrap {
    background-color: $purple_light;
    margin: 30px 0;
    height: calc(100% - 67px);
    position: relative;
}
@media (min-width: $(desktop)) {
    .neighborhood-overview .listings-wrap {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/2 - (30px - 30px * 1/2));
    }
    .neighborhood-overview .listings-wrap:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .neighborhood-overview .listings-wrap:last-child {
        margin-right: 0;
    }
    .neighborhood-overview .listings-wrap:nth-child(1n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.neighborhood-overview .tab-header {
    background-color: $purple_light;
    height: 200px;
    top: 0;
    left: 0;
    right: 0;
    padding: 40px 30px;
    text-align: center;
    width: 100%;
    z-index: 2;
}
.neighborhood-overview .tab-header h2 {
    color: #fff;
    font-size: 40px;
    letter-spacing: -0.9px;
    margin: 0 0 8px;
}
.neighborhood-overview .tab-header p {
    font-family: "Open sans";
    color: #fff;
}
.neighborhood-overview .tabs {
    position: relative;
    margin-top: 107px;
}
.neighborhood-overview .tab-links {
    border-bottom: 4px solid $blue;
    top: 0;
}
.neighborhood-overview .tab-links ul {
    text-align: center;
}
.neighborhood-overview .tab-links li {
    font-family: "Open sans";
    background-color: $purple_dark;
    text-align: center;
    display: inline-block;
    position: relative;
    padding: 0;
    margin: 0 3px;
}
.neighborhood-overview .tab-links li.active {
    background-color: $blue;
}
.neighborhood-overview .tab-links li.active:before{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top:100%;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-top: 14px solid $blue;
    left: 50%;
    margin-left: -7px;
    z-index: 1;
}
.neighborhood-overview .tab-links li:hover {
}
.neighborhood-overview .tab-links a {
    display: block;
    height: 50px;
    padding: 14px 12px;
    color: #fff;
    font-size: 18px;
    letter-spacing: -0.4px;
    text-decoration: none;
}
.neighborhood-overview .tab-bodies-wrap {
    max-height: 100%;
}
.neighborhood-overview .tab-bodies {
    background-color: #fff;
    padding: 30px;
}
.neighborhood-overview .tab-bodies .tab-body {
    position: relative;
}
.neighborhood-overview .tab-bodies .item {
    border-bottom: 2px solid #9b9b9b;
    padding: 0 0 30px;
    margin: 0 0 30px;
}
.neighborhood-overview .tab-bodies .item:last-child {
    border-bottom: 0;
}
.neighborhood-overview .tab-bodies .item .header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0 0 45px;
}
.neighborhood-overview .tab-bodies .item .header .image {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 3/5 - (30px - 30px * 3/5));
    text-align: center;
    margin-bottom: -45px;
}
.neighborhood-overview .tab-bodies .item .header .image:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.neighborhood-overview .tab-bodies .item .header .image:last-child {
    margin-right: 0;
}
.neighborhood-overview .tab-bodies .item .header .image:nth-child(5n) {
    margin-right: 0;
    margin-left: auto;
}
.neighborhood-overview .tab-bodies .item .header .image img {
    display: block;
    height: auto;
    width: 100%;
}
.neighborhood-overview .tab-bodies .item .header .image .map-view {
    margin: 0 auto;
    padding-left: 12px;
    padding-right: 12px;
    top: -25px;
}
.neighborhood-overview .tab-bodies .item .header .title {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 2/5 - (30px - 30px * 2/5));
}
.neighborhood-overview .tab-bodies .item .header .title:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.neighborhood-overview .tab-bodies .item .header .title:last-child {
    margin-right: 0;
}
.neighborhood-overview .tab-bodies .item .header .title:nth-child(5n) {
    margin-right: 0;
    margin-left: auto;
}
.neighborhood-overview .tab-bodies .item .header .title h4 {
    color: $purple;
}
.neighborhood-overview .tab-bodies .item .body {
    text-align: center;
}
.neighborhood-overview .tab-bodies .item .body p {
    font-family: "Open sans";
    font-size: 15px;
    line-height: 1.5;
    text-align: left;
}
.neighborhood-overview .tab-bodies .item .body a.button {
    margin-left: auto;
    margin-right: auto;
}